import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { SanitaizerPipe } from "src/app/sanitaizer.pipe";

@Component({
  selector: "app-sugar",
  templateUrl: "./sugar.component.html",
  styleUrls: ["./sugar.component.scss"]
})
export class SugarComponent implements OnInit {
  iii: number;
  itemtitle: any;
  itemid: any;
  renderer: string;
  nav = false;
  lister: any;
  loader = false;
  constructor(private http: HttpClient) {}

  ngOnInit() {
    this.loader = true;
    this.iii = 0;
    this.http
      .get(
        "https://raw.githubusercontent.com/creativecoderone/poster/master/menu.json"
      )
      .subscribe(
        data => {
          this.lister = data;
          // console.log(data);
          this.itemid = this.lister[0]["id"];
          this.itemtitle = this.lister[0]["title"];
          this.http
            .get(
              "https://raw.githubusercontent.com/creativecoderone/poster/master/" +
                this.lister[0]["id"] +
                ".txt",
              { responseType: "text" }
            )
            .subscribe(data2 => {
              this.loader = false;
              this.renderer = data2;
            });
        },
        err => {}
      );
  }
  getRender(index) {
    // this.loader = true;
    this.itemid = this.lister[index]["id"];
    this.itemtitle = this.lister[index]["title"];
    this.http
      .get(
        "https://raw.githubusercontent.com/creativecoderone/poster/master/" +
          this.lister[index]["id"] +
          ".txt",
        { responseType: "text" }
      )
      .subscribe(
        data2 => {
          this.iii = index;
          this.loader = false;
          this.renderer = data2;
        },
        err => {}
      );
    return index;
  }
  openNav() {
    this.nav = true;
    document.getElementById("myNav").style.width = "100%";
  }

  closeNav() {
    this.nav = false;
    document.getElementById("myNav").style.width = "0%";
  }
  ins() {
    // console.log("inc", this.iii);
    if (this.iii < this.lister.length - 1) {
      this.loader = true;
      this.iii = this.iii + 1;
      this.itemid = this.lister[this.iii]["id"];
      this.itemtitle = this.lister[this.iii]["title"];
      this.http
        .get(
          "https://raw.githubusercontent.com/creativecoderone/poster/master/" +
            this.lister[this.iii]["id"] +
            ".txt",
          { responseType: "text" }
        )
        .subscribe(
          data2 => {
            this.loader = false;
            this.renderer = data2;
          },
          err => {}
        );
    }
  }
  dis() {
    // console.log("dis", this.iii);
    if (this.iii > 0) {
      this.loader = true;
      this.iii = this.iii - 1;
      this.itemid = this.lister[this.iii]["id"];
      this.itemtitle = this.lister[this.iii]["title"];
      this.http
        .get(
          "https://raw.githubusercontent.com/creativecoderone/poster/master/" +
            this.lister[this.iii]["id"] +
            ".txt",
          { responseType: "text" }
        )
        .subscribe(
          data2 => {
            this.loader = false;
            this.renderer = data2;
          },
          err => {}
        );
    }
  }
}
